import React from 'react';
import pammy from '../../image/pammy.jpg'
import okoro from '../../image/okoro.jpg'
import nqwobi from '../../image/nqwobi.png'
import oji from '../../image/ojiOse.jpg'
import eju from '../../image/eju.jpg'
import abacha from '../../image/abacha.jpg'
import styled from 'styled-components'

const menuItems = [
    {
      id:1,
      title: 'Pammy',
      description: 'Palm wine is an alcoholic Nigerian side beverage that plays an essential role in traditional activities in Igbo lands. Similarly to Kolanut, it is used for rituals and traditional marriage ceremonies',
      imageUrl:pammy,
      linkUrl: 'https://example.com/item1',

    },
    {
        id:2,
      title: 'Abacha',
      description: 'Abacha, commonly known as African salad is a dish made from cassava but what makes it unique is the ingredients used during the preparation. A typical Abacha meal is best prepared with a variety of dried fishes but heres a complete version of the Abacha with all the possible ingredients',
      imageUrl: abacha,
      linkUrl: 'https://example.com/item2',

    },
    {
        id:3,
      title: 'Nqwobi',
      description: 'There are some types of dishes that are associated with peoples origins. Nkwobi is a good example as its origin can be traced back to the Igbo tribe the eastern part of Nigeria. Formerly, bush meats were used to prepare Nqwobi. But nowadays, Nqwobi can be prepared using cow legs, spicy sauces, and herbs such as Utazi-leaf and Nchanwu. The sauce is thickened with a chemical substance called Okanwu, which actually foams up the stew. The instance is traditionally referred to as Igbangu. Nqwobi is considered an antidote to alcohol intoxication, and as such, it is preferablly taken with alcohol',
      imageUrl:nqwobi,
      linkUrl: 'https://example.com/item3',

    },
    {
        id:4,
      title: 'Kolanut',
      description: 'Kolanut which is also known as Oji in Igbo land is historical significant and the instances cannot be overstated due to its numerous roles. it represents acceptance, cooperation, and solidarity. Guests are usually offered it as a way to make them feel welcome by their hosts',
      imageUrl: oji,
      linkUrl: 'https://example.com/item4',

    },
    {
        id:5,
      title: 'Eju',
      description: 'In addition to containing significant sources of protein and low amounts of fat, snails are also good sources of iron, calcium, Vitamin A, and a number of other minerals. Vitamin A helps your immune system fight off diseases and strengthens your eyes. It also helps cells in your body grow.',
      imageUrl: eju,
      linkUrl: 'https://example.com/item5',

    },
    {
        id:6,
      title: 'Okoro-Soup',
      description: 'Okro soup is prepared using the edible green seed pods of the okra flowering plant as a primary ingredient. traditionally cooked in a delicious mixture of palm oil, shrimps, goat meat, fish, and African spices and simmered to perfection!',
      imageUrl:okoro,
      linkUrl: 'https://example.com/item6',


    },
  ];

const Food2 = () => {
  return (
    <>
    
    
 <FOOD className="menu-container relative d-flex flex-column">
      <header> 
         <h1 className="menu-header text-dark p-2">Igbo Traditional Foods</h1>
      </header>
      <div className="menu-grid">
        {menuItems.map((item) => (
          <div key={item.id} href={item.linkUrl}  className="menu-item">
            <img src={item.imageUrl} alt={item.title} />
            <h2>{item.title}</h2>
            <p>{ item.description.slice(0, 250)}.....
            <a className='link relative left-2' href={item.linkUrl} >Readmore</a></p>
          </div>
        ))}
      </div>
    </FOOD>
    </>
  );
};

export default Food2;

const FOOD = styled.div`

    max-width: 800px;
    margin: 0 auto;
    padding: 50px 0;
    @media(max-width:992px){
      transform: translateY(-3.5rem) !important;
    }


    h1{
    
        background-color: red;
        background-image: linear-gradient(45deg, #f3ec78, #af4261);
        background-size: 100%;
        background-repeat: repeat;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent; 
        -moz-background-clip: text;
        -moz-text-fill-color: transparent;
        font-size: 2.5rem !important;
        font-weight: bold;
        @media(max-width:992px){
        padding: 2rem 0 !important;
       }
        
      }
  
  
    header{
      background-color: #181123;
      text-align: center;
      margin-bottom: 50px;

  }

  
  .menu-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin: 0 auto;
    @media(max-width:992px){
      transform: translateY(-1.2rem) !important;
    }
  }
  
  .menu-item {
    position: relative;
    cursor: pointer;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

    .link{
        z-index: 100;
        text-decoration: none;
        color: red;
        font-size: 1.2rem;
       font-family: Verdana, Geneva, Tahoma, sans-serif;   
    }
  }
  
  .menu-item img {
    width: 100%;
    height: auto;
    transition: all 0.3s ease;
  }
  
  .menu-item:hover img {
    transform: scale(1.1);
  }
  
  .menu-item h2 {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    font-size: 24px;
    padding: 10px;
    margin: 0;
  }
  
  .menu-item p {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 200px !important;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff !important;
    font-size: 16px;
    padding: 10px;
    margin: 0;
    opacity: 0;
    transition: all 0.3s ease;
    text-align: center;
  }
  
  .menu-item:hover p {
    opacity: 1;
  }
  
  /* For screens smaller than 768px (mobile devices) */
@media (max-width: 767px) {
    .menu-header {
      font-size: 28px;
    }
  
    .menu-grid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      grid-gap: 20px;
    }
  
    .menu-item-details {
      font-size: 15px;
    }
  }
  
  /* For screens between 768px and 1024px (tablets) */
  @media (min-width: 768px) and (max-width: 1024px) {
    .menu-grid {
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      grid-gap: 25px;
    }
  }
  
  /* For screens larger than 1024px (desktops) */
  @media (min-width: 1025px) {
    .menu-grid {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      grid-gap: 30px;
    }
  }
  

`